.Home {
  text-align: center;
}


.Home-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}


a {
font-family: Vollkorn, serif;
text-decoration:none;
  color: #000000;

}


.avatar {
  border-radius: 10px;
  height: 22vh; 
  margin-bottom: 1vh;
  /* box-shadow: 1px 1px 1px #919191; */
}

.avatar:hover {
  animation: wiggle .6s;
  /* box-shadow: 2px 2px 1px #919191; */
  outline: none;
}

.avatar:focus {
  outline: none;
}

.avatarPapers {
  border-radius: 10px;
  height: 18vh; 
  margin-bottom: 1vh;
}

.avatarPapers:hover {
  /* box-shadow: 1px 1px 1px #919191; */
  animation: wiggle .6s;
}

.papersMobile {
  
}

.bibList {
  list-style-type: none;
}

.avatarRow {
  margin-top: 4vh;
}


.socialLinkRow {
  margin-top: 3vh;
}

.aboutText {
  text-align: justify;
  font-size: 1.1em;
  margin-top: 1vh;
}

.bibEntry {
  text-align: justify;
  font-size: 1.1em;
  font-family: 'Vollkorn', serif;
  margin: 1.4em;
}

.socialLink {
  padding: 0.5vw 0.5vw 0.5vw 0.5vw;
}

.papersMenuItem {
  font-family: 'Open Sans', sans-serif; 
  font-size: 0.8em;
}

.myName {
  margin-top: 7vh;
  font-family: 'Vollkorn', serif;
}

.myNamePapers {
  margin-top: 5vh;
  font-family: 'Vollkorn', serif;
}

.myName:hover {
  /* text-shadow: 0.0px 0.0px 0.2px #45778d; */
  color: #6b1f1f;
  /* animation: wiggle .6s; */
}

.navBtn {
  font-family: 'Open Sans', sans-serif;
  font-weight: 100;
  margin-top: 1vh;
  /* border: 0; */
}


.aboutText {
  font-family: 'Vollkorn', serif;
  font-weight: 100;
}


@keyframes wiggle {

  0% { transform: rotate(0deg); }
  16.66% { transform: rotate(-.8deg); }
  33.32% { transform: rotate(.8deg); }
  50% { transform: rotate(0deg); }
  66.64% { transform: rotate(.8deg); }
  83.3% { transform: rotate(-.8deg); }
  100% { transform: rotate(0deg); } 
  /* 70% { transform: rotate(-.8deg); } */
  /* 80% { transform: rotate(.8deg); } */
  /* 90% { transform: rotate(0deg); }
  100% { transform: rotate(-.8deg); } */
}